.article-main {
  position: relative;
  background: #182030;
  padding: 150px 0 30px;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: -10px;
    left: -650px;
    width: 1150px;
    height: 1150px;
    background: rgba(5, 192, 204, 0.89);
    mix-blend-mode: overlay;
    filter: blur(150px);
    border-radius: 50%;
    opacity: 0.7;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    position: relative;
    z-index: 2;
    width: calc(100% - 530px);
  }
  &__right {
    width: 470px;
    margin-left: 60px;
  }
  &__text {
    max-width: 530px;
    margin-top: 32px;
    p {
      font-size: 18px;
      line-height: 130%;
      text-shadow: 0px 2px 6px rgba(0, 0, 0, 0.60);
      color: #E5E7ED;
    }
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 32px;
  }
  &__btn {
    width: 240px;
    height: 44px;
    margin-right: 16px;
    margin-bottom: 10px;
  }
  &__btn2 {
    width: 240px;
    height: 44px;
    margin-bottom: 10px;
  }
  &__img {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 800px;
      height: 800px;
      background: #5833C2;
      mix-blend-mode: overlay;
      filter: blur(150px);
      border-radius: 50%;
    }
    img {
      position: relative;
      z-index: 2;
      width: 100%;
      border-radius: 10px;
    }
  }
  &__items {
    position: relative;
    z-index: 2;
    display: flex;
    flex-wrap: wrap;
    margin-top: 50px;
  }
  &__item {
    position: relative;
    margin-bottom: 30px;
    &:not(:last-child) {
      margin-right: 60px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -29px;
        transform: translate(0, -50%);
        width: 1px;
        height: 100%;
        border: 1px dashed rgba(141, 150, 162, 0.36);
      }
    }
  }
  &__name {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 28px;
      margin-right: 10px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 130%;
      font-weight: 500;
      text-shadow: 0px 2px 6px rgba(0, 0, 0, 0.60);
      color: rgba(229, 231, 237, 0.90);
    }
  }
  &__verified {
    display: flex;
    align-items: center;
    margin-top: 5px;
    i {
      flex-shrink: 0;
      width: 12px;
      margin-right: 7px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 14px;
      line-height: 130%;
      text-shadow: 0px 2px 6px rgba(0, 0, 0, 0.60);
      color: rgba(229, 231, 237, 0.50);
      a {
        color: #07AAE5;
        text-decoration: underline;
        text-underline-offset: 3px;
      }
    }
  }
  &__rating {
    display: flex;
    align-items: center;
    &-text {
      font-size: 14px;
      line-height: 130%;
      text-shadow: 0px 2px 6px rgba(0, 0, 0, 0.60);
      color: rgba(229, 231, 237, 0.50);
    }
  }
  &__stars {
    display: flex;
    align-items: center;
    margin: 0 8px;
  }
  &__star {
    width: 12px;
    &:not(:first-child) {
      margin-left: 8px;
    }
    img {
      width: 100%;
    }
  }
  &__date {
    font-size: 14px;
    line-height: 130%;
    text-shadow: 0px 2px 6px rgba(0, 0, 0, 0.60);
    color: rgba(229, 231, 237, 0.50);
    margin-top: 5px;
  }
  &__info {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-top: 5px;
    }
    i {
      flex-shrink: 0;
      width: 13px;
      margin-right: 7px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 14px;
      line-height: 130%;
      text-shadow: 0px 2px 6px rgba(0, 0, 0, 0.60);
      color: rgba(229, 231, 237, 0.50);
    }
  }
}
.article-info {
  background: #F8FAFF;
  padding: 80px 0 30px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__img {
    width: 460px;
    margin-right: 50px;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: calc(100% - 510px);
    padding: 0;
  }
  &__title {
    font-size: 28px;
    line-height: 130%;
    font-weight: 700;
    color: #182030;
  }
  .tool-card__list {
    li {
      color: #182030;
    }
    li:after {
      background: #182030;
    }
  }
  .tool-card__info {
    color: rgba(24, 32, 48, 0.47);
  }
  .tool-card__btn2 {
    border: 1px solid rgba(24, 32, 48, 0.14);
    color: rgba(24, 32, 48, 0.71);
    &:hover {
      background: rgba(24, 32, 48, 0.14) !important;
    }
  }
}
.article {
  background: #F8FAFF;
  padding: 30px 0 120px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__content {
    width: calc(100% - 270px);
    color: #2F3B52;
    overflow: hidden;
  }
  &__info {
    text-align: center;
    font-size: 16px;
    line-height: 130%;
    color: rgba(24, 32, 48, 0.47);
    margin-top: 10px;
  }
  &__btns {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__btn {
    width: 238px;
    height: 48px;
    margin-right: 15px;
  }
  &__btn2 {
    width: 238px;
    height: 48px;
    border: 1px solid rgba(24, 32, 48, 0.14) !important;
    color: rgba(24, 32, 48, 0.71) !important;
    &:hover {
      color: #f8faff !important;
    }
  }
  &__btn3 {
    width: 240px;
    height: 48px;
    margin: 0 auto;
  }
  &__flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    .article__btns {
      margin-bottom: 10px;
    }
  }
  &__logo {
    width: 245px;
    display: block;
    margin-right: 15px;
    margin-bottom: 10px;
  }
  &__list {
    li {
      padding-left: 0 !important;
    }
    li:after {
      display: none;
    }
  }
  &__list2 {
    grid-template-columns: repeat(3, calc(100% / 3 - 120px / 3 )) !important;
    gap: 60px !important;
    li {
      padding-left: 48px !important;
      &:after {
        width: 38px !important;
        height: 38px !important;
        display: block;
        background-image: url("../img/li-icon2.svg") !important;
      }
      span {
        display: block;
      }
    }
  }
  &__card {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #2B3450;
    color: #FBFBFB;
    border-radius: 16px;
    padding: 30px;
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      right: -500px;
      bottom: -500px;
      width: 700px;
      height: 700px;
      background: linear-gradient(90deg, rgba(0, 219, 222, 0.70) 0%, rgba(252, 0, 255, 0.70) 100%);
      filter: blur(177.25px);
      border-radius: 50%;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      top: -500px;
      left: -500px;
      width: 700px;
      height: 700px;
      background: linear-gradient(90deg, rgba(0, 219, 222, 0.70) 0%, rgba(252, 0, 255, 0.70) 100%);
      filter: blur(178.39999389648438px);
      border-radius: 50%;
      pointer-events: none;
    }
    &-img {
      position: relative;
      z-index: 1;
      width: 300px;
      margin-right: 30px;
    }
    &-desc {
      position: relative;
      z-index: 1;
      width: calc(100% - 330px);
    }
    h3 {
      margin-bottom: 16px;
    }
    &-btn {
      width: 240px;
      height: 48px;
      margin: 32px 0 0 0;
    }
  }
  &-advantages__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 20px / 2));
    gap: 20px;
  }
  &-advantages__item {
    border-radius: 16px;
    padding: 20px 25px 40px;
    &:nth-child(1) {
      background: rgba(100, 204, 123, 0.08);
      border: 1px solid #E0E5F0;
    }
    &:nth-child(2) {
      background: rgba(255, 143, 143, 0.08);
      border: 1px solid rgba(255, 143, 143, 0.15);
    }
  }
  &-table {
    overflow-x: auto;
    &::-webkit-scrollbar {
      height: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #182030;
      border-radius: 3px;
    }
    &__logo {
      width: 140px;
      display: block;
      img {
        width: 100%;
      }
    }
    &__show {
      display: inline-flex;
      align-items: center;
      cursor: pointer;
      margin-top: 30px;
      i {
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 36px;
        height: 36px;
        background: rgba(30, 37, 59, 0.21);
        filter: drop-shadow(0px 16px 50px rgba(75, 78, 83, 0.40));
        border-radius: 50%;
        margin-right: 12px;
      }
      i svg {
        width: 12px;
        transition: all 0.3s;
      }
      span {
        font-size: 18px;
        line-height: 130%;
      }
    }
    &__btn {
      width: 160px;
      height: 40px;
      span {
        font-size: 14px !important;
      }
    }
  }
  &-table table {
    width: 870px;
    border-collapse: collapse;
    thead {
      tr th:nth-child(1) {
        text-align: start;
      }
      tr th {
        text-align: center;
        font-size: 16px;
        line-height: 130%;
        font-weight: 700;
        padding: 20px 15px;
      }
    }
    tbody {
      tr {
        border-top: 1px dashed rgba(141, 150, 162, 0.30);;
      }
      tr td {
        text-align: center;
        font-size: 16px;
        line-height: 130%;
        padding: 20px 15px;
      }
    }
    .table-hide {
      display: none;
    }
  }
  &-info__items {
    background: #FFF;
    border-radius: 16px;
    border: 1px solid #E9EDF5;
    padding: 30px 40px;
  }
  &-info__item {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
    border-top: 1px dashed rgba(141, 150, 162, 0.30);
    &:nth-child(1) {
      border-top: none;
      padding-top: 0;
    }
    &-title {
      font-size: 16px;
      line-height: 130%;
      font-weight: 700;
      margin-right: 10px;
    }
    &-text {
      max-width: 370px;
      width: 100%;
      p {
        font-size: 16px !important;
      }
    }
    &-list {
      max-width: 370px;
      width: 100%;
      li {
        font-size: 16px !important;
      }
      li:after {
        top: 10px !important;
        width: 5px !important;
        height: 5px !important;
        background: #182030;
        background-image: none !important;
        border-radius: 50%;
      }
    }
  }
  .editorial-office__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  .editorial-office__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #E0E5F0;
    border-radius: 16px;
    padding: 10px;
    &-left {
      display: flex;
      align-items: center;
      margin-right: 24px;
    }
    &-right {
      flex-shrink: 0;
    }
    &-img {
      flex-shrink: 0;
      width: 150px;
      margin-right: 20px;
    }
    &-subtitle {
      font-size: 13px;
      color: rgba(24, 32, 48, 0.47);
    }
    &-title {
      font-size: 18px;
      line-height: 130%;
      margin-top: 3px;
    }
    &-text {
      font-size: 16px;
      line-height: 130%;
      margin-top: 3px;
    }
    &-btn {
      width: 150px;
      height: 40px;
    }
  }
  &-conclusion__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 40px;
    padding-left: 7px;
  }
  &-conclusion__item {
    display: flex;
    &-number {
      position: relative;
      flex-shrink: 0;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 15px;
      line-height: 130%;
      font-weight: 500;
      margin-right: 15px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transform: rotate(-45deg);
        width: 100%;
        height: 100%;
        border: 1px solid #26E2E7;
      }
    }
    &-title {
      font-size: 18px;
      line-height: 130%;
      span {
        font-size: 22px;
        font-weight: 700;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 150%;
      margin-top: 10px;
    }
    .article__btns {
      justify-content: start;
      margin: 20px 0 0 0;
    }
    &-btn {
      width: 170px;
      height: 40px;
      margin-top: 20px;
    }
    &-btn2 {
      width: 160px;
      height: 40px;
      margin: 20px 0 0 0;
      span {
        font-size: 14px !important;
      }
    }
  }
  &-questions__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &-questions__item {
    background: #FFF;
    border: 1px solid #E9EDF5;
    border-radius: 16px;
    padding: 30px 40px;
    &-title {
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
    }
    &-text {
      font-size: 16px;
      line-height: 150%;
      margin-top: 12px;
    }
  }
  .articles {
    padding: 0;
  }
  .articles__slider {
    padding: 0;
    margin: 0;
  }
  .article-card__title {
    color: #2f3b52;
  }
  .article-card__text {
    color: #2f3b52;
  }
  &__img {
    position: relative;
    display: block;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 16px;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.30) 0%, rgba(0, 0, 0, 0.30) 100%);
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 60px;
      height: 60px;
      background-image: url("../img/icon.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:hover {
      &:after,
      &:before {
        opacity: 1;
        pointer-events: all;
      }
    }
    img {
      border-radius: 16px;
    }
  }
}
.article__sidebar {
  position: relative;
  width: 250px;
  margin-right: 20px;
  &-menu {
    position: sticky;
    top: 15px;
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      padding-left: 20px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1px;
        height: 100%;
        background: #E9EDF5;
      }
    }
    li {
      font-size: 13px;
      line-height: 130%;
      color: rgba(24, 32, 48, 0.47);
      cursor: pointer;
    }
    li a {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: -21px;
        width: 3px;
        height: 100%;
        background: #454C59;
        border-radius: 3px;
        opacity: 0;
        pointer-events: none;
      }
    }
    li .active {
      &:after {
        opacity: 1;
        pointer-events: all;
      }
    }
  }
  .tab--active {
    color: rgba(24, 32, 48, 0.80);
    &:after {
      opacity: 1;
      pointer-events: all;
    }
  }
}
.article__group {
  &:not(:last-child) {
    margin-bottom: 60px;
  }
  h2 {
    font-size: 36px;
    line-height: 130%;
    margin-bottom: 30px;
  }
  h3 {
    font-size: 28px;
    line-height: 130%;
  }
  h4 {
    font-size: 24px;
    line-height: 130%;
    margin-bottom: 15px;
  }
  p {
    font-size: 18px;
    line-height: 150%;
  }
  nav {
    border: 1px solid #E9EDF5;
    border-radius: 16px;
    padding: 30px;
    margin-bottom: 30px;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li {
      position: relative;
      font-size: 18px;
      line-height: 130%;
      padding-left: 24px;
      &:after {
        content: '';
        position: absolute;
        top: 5px;
        left: 0;
        width: 14px;
        height: 14px;
        background-image: url("../img/li-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
  }
  .text-bg {
    background: #ECEFF5;
    border-radius: 16px;
    padding: 30px 40px;
  }
  hr {
    height: 30px;
    margin: 0;
    opacity: 0;
    pointer-events: none;
  }
  .uploading-btn {
    max-width: 240px;
    height: 48px;
    margin: 0 auto;
  }
  img {
    width: 100%;
  }
}



@media screen and (max-width: 1150px) {
  .article-main {
    &__left {
      width: calc(100% - 510px);
    }
    &__right {
      margin-left: 40px;
    }
  }
  .article {
    &__list2 {
      grid-template-columns: repeat(2, calc(50% - 60px / 2)) !important;
    }
    &-info__item {
      display: block;
      &-title {
        margin: 0 0 10px 0;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .article-main {
    padding-top: 120px;
    &__wrap {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      max-width: 400px;
      margin: 40px auto 0;
    }
    &__items {
      margin-top: 30px;
    }
  }
  .article-info {
    padding: 60px 0 30px;
    &__inner {
      display: block;
    }
    &__img {
      width: 100%;
      max-width: 400px;
      margin: 0 auto 40px;
    }
    &__desc {
      width: 100%;
    }
    &__title {
      font-size: 24px;
    }
  }
  .article {
    padding: 30px 0 60px;
    &__inner {
      display: block;
    }
    &__sidebar {
      width: 100%;
      margin: 0 0 40px 0;
      &-item {
        font-size: 16px;
      }
    }
    &__content {
      width: 100%;
    }
    &__card {
      display: block;
      padding: 30px 15px;
      &-img {
        width: 100%;
        max-width: 400px;
        margin: 0 auto 30px;
      }
      &-desc {
        width: 100%;
      }
    }
    &__list2 {
      gap: 15px !important;
    }
    &-advantages__item {
      padding: 30px 15px;
    }
    &-table table {
      width: 962px;
    }
    &-info__items {
      padding: 30px 15px;
    }
    .editorial-office__item {
      display: block;
      &-left {
        margin: 0 0 16px 0;
      }
      &-right {
        display: flex;
        justify-content: flex-end;
      }
    }
    &-conclusion__items {
      gap: 30px;
    }
    &-conclusion__item {
      &-title {
        font-size: 16px;
        span {
          font-size: 18px;
        }
      }
    }
    &-questions__item {
      padding: 15px;
    }
  }
  .article__group {
    h2 {
      font-size: 28px;
      margin-bottom: 20px;
    }
    h3 {
      font-size: 24px;
    }
    p {
      font-size: 16px;
    }
    nav {
      padding: 15px;
      margin-bottom: 20px;
    }
    ul {
      li {
        font-size: 16px;
      }
    }
    .text-bg {
      padding: 15px;
    }
    hr {
      height: 20px;
    }
  }
}

@media screen and (max-width: 768px) {
  .article {
    &-advantages__items {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .article-main {
    &__items {
      display: block;
    }
    &__item {
      margin-bottom: 0;
      &:not(:last-child) {
        margin: 0 0 30px 0;
        &:after {
          display: none;
        }
      }
    }
  }
  .article {
    &__btns {
      flex-direction: column;
    }
    &__btn {
      margin: 0 0 15px 0;
    }
    &__flex {
      display: block;
      .article__btns {
        margin-bottom: 0;
      }
    }
    &__logo {
