.main {
  position: relative;
  padding: 120px 0 60px;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: -900px;
    width: 1500px;
    height: 1500px;
    background: #05C0CC;
    mix-blend-mode: overlay;
    filter: blur(150px);
    border-radius: 50%;
    opacity: 0.7;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    position: relative;
    z-index: 1;
    text-align: center;
    font-size: 56px;
    line-height: 130%;
    color: #F8FAFF;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
  }
  &__left {
    width: calc(100% - 490px);
  }
  &__right {
    position: relative;
    z-index: 1;
    width: 460px;
    text-align: center;
    margin-left: 30px;
  }
  &__img {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 1000px;
      height: 1000px;
      background: #5833C2;
      border-radius: 50%;
      mix-blend-mode: overlay;
      filter: blur(150px);
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
  &__subtitle {
    font-size: 24px;
    line-height: 130%;
    font-weight: 700;
    color: #E5E7ED;
  }
  .uploading {
    margin-top: 30px;
  }
  .uploading-icon {
    margin: 16px auto 0;
  }
  .uploading-btn {
    width: 200px;
    height: 48px;
    margin: 16px auto 0;
  }
}
.uploading {
  position: relative;
  background: #17283E;
  border: 2px dashed #626876;
  border-radius: 8px;
  padding: 30px;
  cursor: pointer;
  &:hover {
    border-color: #1c77ff;;
    .uploading-btn {
      background: linear-gradient(90deg, #189625 0.39%, #11D147 100%);
    }
  }
  &-desc {
    pointer-events: none;
  }
  &-title {
    font-size: 18px;
    line-height: 130%;
    font-weight: 500;
    color: #FBFBFB;
    span {
      color: #1C77FF;
    }
  }
  &-icon {
    width: 50px;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  &-info {
    font-size: 16px;
    line-height: 130%;
    color: #CED2DC;
    margin-top: 24px;
  }
  &-btn2 {
    position: relative;
    input {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:hover ~ button {
      background: #142031;
    }
    button {
      width: 100%;
      height: 100%;
      border: 1px solid rgba(52, 68, 88, 0.35);
      background: #1D2A42;
      box-shadow: 0px 0px 39.478px 0px rgba(15, 146, 202, 0.25);
      i {
        flex-shrink: 0;
        width: 17px;
        margin-right: 8px;
        svg {
          width: 100%;
        }
      }
      span {
        font-size: 13px;
        line-height: 130%;
        color: #D6D6D6;
      }
    }
  }
  .dz-preview {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #17283E;
    padding: 15px;
    pointer-events: none;
  }
  .dz-size {
    color: #FBFBFB;
  }
  .dz-filename {
    color: #FBFBFB;
  }
  .dz-error-message {
    color: #FBFBFB;
  }
  .dz-success-mark {
    display: none;
  }
  .dz-error-mark {
    display: none;
  }

}
.main__images {
  display: grid;
  grid-template-columns: repeat(4, calc(25% - 48px / 4));
  gap: 16px;
  margin-top: 16px;
  &-group {
    text-align: center;
    margin-top: 24px;
  }
  &-title {
    font-size: 14px;
    line-height: 130%;
    color: #8D95A3;
  }
  img {
    width: 100%;
    border-radius: 4px;
  }
}
.advantages {
  position: relative;
  z-index: 2;
  padding: 60px 0 130px;
  overflow: hidden;
  &__slider {
    position: relative;
    margin-top: 40px;
  }
  &__swiper {
    overflow: unset;
  }
  .swiper-pagination {
    bottom: -60px;
  }
}
.advantages-slide {
  &:hover {
    .advantages-slide__title {
      text-decoration: underline;
    }
  }
  &__top {
    position: relative;
  }
  &__img {
    position: relative;
    padding-bottom: 66%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 8px;
    }
  }
  &__icon {
    position: absolute;
    right: 10px;
    bottom: 10px;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #5833C2;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.25);
    border-radius: 6px;
    img {
      max-width: 28px;
      max-height: 100%;
    }
  }
  &__desc {
    margin-top: 16px;
  }
  &__title {
    text-align: center;
    font-size: 16px;
    line-height: 130%;
    font-weight: 500;
    color: #ffffff;
  }
}
.neuralnetwork {
