.tool-card {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  background: linear-gradient(96deg, rgba(30, 37, 60, 0.50) 5.68%, rgba(30, 37, 60, 0.10) 98.86%);
  border: 1px solid rgba(67, 75, 92, 0.30);
  backdrop-filter: blur(30px);
  border-radius: 16px;
  &__images {
    width: 520px;
    height: 100% !important;
    margin-right: 40px;
  }
  &__img {
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      border-radius: 8px;
    }
  }
  &__desc {
    width: calc(100% - 560px);
    padding: 40px 60px;
  }
  &__title {
    font-size: 24px;
    line-height: 130%;
    font-weight: 700;
    background: linear-gradient(180deg, #FA9A29 0%, #DB6E1F 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  &__text {
    font-size: 18px;
    line-height: 130%;
    color: #CED2DC;
    margin-top: 40px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-top: 24px;
    li {
      position: relative;
      font-size: 16px;
      line-height: 130%;
      color: #F4F4F7;
      padding-left: 12px;
      &:after {
        content: '';
        position: absolute;
        top: 7px;
        left: 0;
        width: 5px;
        height: 5px;
        background: #f4f4f7;
        border-radius: 50%;
      }
    }
  }
  &__btn {
    min-height: 48px;
    display: inline-flex !important;
    padding: 3px 24px;
    margin-top: 24px;
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    margin-top: 24px;
  }
  &__info {
    font-size: 18px;
    line-height: 130%;
    color: #CED2DC;
  }
}
.tool-card:nth-child(even) {
  .tool-card__images {
    order: 1;
    margin: 0 0 0 40px;
  }
}
