}
.feedback {
  background: #1E253C;
  padding: 100px 0;
  overflow: hidden;
  &__slider {
    position: relative;
    padding-left: 25px;
    margin-top: 60px;
  }
  &__swiper {
    overflow: unset;
  }
  &__swiper-wrapper {
    align-items: stretch;
  }
  &__slider-nav {
    display: flex;
    align-items: center;
    margin-top: 60px;
  }
  .swiper-button-prev {
    position: static;
  }
  .swiper-button-next {
    position: static;
    margin-left: 24px;
  }
  .swiper-slide-active {
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: -15px;
      transform: translate(0, -50%);
      width: calc(100% - 22px);
      height: calc(100% - 22px);
      background: #382979;
      border-radius: 24px;
      transition: all 0.5s;
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: -24px;
      transform: translate(0, -50%);
      width: calc(100% - 22px);
      height: calc(100% - 42px);
      background: #25244D;
      border-radius: 24px;
      transition: all 0.5s;
    }
    &:hover {
      &:after {
        background: #e3e3e3;
      }
      &:before {
        background: #bdbdbd;
      }
    }
  }
}
.feedback-card {
  position: relative;
  max-width: 560px;
  height: auto;
  color: #FBFBFB;
  border-radius: 24px;
  @media screen and (min-width: 993px) {
    transform: perspective(560px) rotateY(4deg) !important;
  }
  &:hover {
    color: #182030;
    .feedback-card__wrap {
      background: #FBFBFB;
    }
  }
  &__wrap {
    position: relative;
    z-index: 2;
