.tools-main {
  position: relative;
  background: #1E253B;
  padding: 150px 0 60px;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: -300px;
    left: -600px;
    width: 1100px;
    height: 1100px;
    background: rgba(5, 192, 204, 0.89);
    mix-blend-mode: overlay;
    filter: blur(150px);
    border-radius: 50%;
    opacity: 0.7;
    pointer-events: none;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    position: relative;
    z-index: 2;
    width: calc(100% - 540px);
    max-width: 525px;
    .btn-green {
      max-width: 240px;
      height: 48px;
      margin-top: 32px;
    }
  }
  &__right {
    width: 500px;
    margin-left: 40px;
  }
  &__text {
    margin-top: 20px;
    p {
      font-size: 18px;
      line-height: 130%;
      text-shadow: 0px 2px 6px rgba(0, 0, 0, 0.60);
      color: #CED2DC;
    }
  }
  &__wrap {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 800px;
      height: 800px;
      background: #5833C2;
      mix-blend-mode: overlay;
      filter: blur(150px);
      border-radius: 50%;
      pointer-events: none;
    }
  }
  &__images {
    position: relative;
    z-index: 2;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    top: -24px;
    left: -24px;
    width: 48px;
    img {
      width: 100%;
    }
  }
  .btn__dark-blue {
    position: absolute;
    z-index: 2;
    width: 190px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #1D2A42;
    border: 0.945px solid rgba(52, 68, 88, 0.35);
    box-shadow: 0px 0px 39.478px 0px rgba(15, 146, 202, 0.25);
    border-radius: 4px;
    i {
      flex-shrink: 0;
      max-width: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 7px;
      svg {
        max-width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 13px;
      line-height: 120%;
      color: #D6D6D6;
    }
  }
  &__btn {
    left: -70px;
    bottom: 110px;
  }
  &__btn2 {
    top: 50px;
    right: -120px;
  }
  &__btn3 {
    right: -90px;
    bottom: 60px;
  }
}


@media screen and (max-width: 1500px) {
  .tools-main {
    @media screen and (min-width: 993px) {
      &__btn {
        left: 0;
        bottom: 0;
      }
      &__btn2 {
        top: unset;
        right: 0;
        bottom: 0;
      }
      &__btn3 {
        right: 0;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .tools-main {
    padding-top: 120px;
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      max-width: 100%;
    }
    &__right {
      width: 100%;
      max-width: 400px;
      margin: 40px auto 0;
    }

  }
}

@media screen and (max-width: 768px) {
  .tools-main {
    &__right {
      max-width: 300px;
    }
  }
}

@media screen and (max-width: 576px) {
  .tools-main {
    &__btn {
      left: -15px;
      bottom: 80px;
    }
    &__btn2 {
      top: 10px;
      right: -15px;
    }
    &__btn3 {
      right: -15px;
      bottom: 20px;
    }
  }
}