.btn-download__windows {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(90deg, #0D9ECC 0.39%, #0051AF 100%);
  border-radius: 30px;
  color: #F8FAFF;
  i {
    flex-shrink: 0;
    width: 18px;
    margin-right: 10px;
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      fill: currentColor;
    }
  }
  span {
    font-size: 16px;
    line-height: 120%;
  }
  &:hover {
    background: linear-gradient(90deg, #0051AF 0.39%, #0D9ECC 100%);
  }
}
.btn-download__mac {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: transparent;
  border: 1px solid #434B5C;
  border-radius: 30px;
  color: #CED2DC;
  i {
    flex-shrink: 0;
    width: 18px;
    margin-right: 10px;
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      fill: currentColor;
    }
  }
  span {
    font-size: 16px;
    line-height: 120%;
  }
  &:hover {
    background: #434B5C;
  }
}
.btn-green {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: linear-gradient(90deg, #11D147 0.39%, #189625 100%);
  box-shadow: 0px 10px 30px 0px rgba(18, 197, 64, 0.40);
  border-radius: 30px;
  color: #FBFBFB;
  i {
    flex-shrink: 0;
    max-width: 20px;
    margin-right: 14px;
    svg {
      max-width: 100%;
      height: auto;
    }
  }
  span {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
  }
  &:hover {
    background: linear-gradient(90deg, #189625 0.39%, #11D147 100%);
  }
}
.btn-green__outline {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: transparent;
  border: 1px solid #11D147;
  border-radius: 30px;
  span {
    line-height: 130%;
    color: rgba(24, 32, 48, 0.71);
  }
  &:hover {
    background: #11D147;
    span {
      color: #fbfbfb;
    }
  }
}
.btn-fixed {
  position: fixed;
  z-index: 5;
  right: 20px;
  bottom: 200px;
  width: 285px;
  height: 55px;
  background: linear-gradient(90deg, #5F72BD 0%, #9B23EA 100%);
  box-shadow: 0px 10px 30px 0px rgba(103, 53, 183, 0.40);
  border-radius: 33px;
  i {
    flex-shrink: 0;
    width: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 8px;
  }
  span {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    color: #FBFBFB;
  }
  &:hover {
    background: linear-gradient(90deg, #9B23EA 0%, #5F72BD 100%);
  }
}
.display2 {
  font-size: 44px;
  line-height: 130%;
  color: #FBFBFB;
  text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.60);
}
.swiper-pagination {
    &-bullet {
      width: 16px;
      height: 16px;
      background: transparent;
      border: 2px solid #434B5C;
      opacity: 1;
    }
  &-bullet-active {
    width: 52px;
    background: #1C77FF;
    border-color: transparent;
    border-radius: 8px;
  }
}
.swiper-button-prev {
  transform: rotate(-180deg);
  width: 40px;
  height: auto;
  margin-top: 0;
  img {
    width: 100%;
  }
  &:after {
    display: none;
  }
}
.swiper-button-next {
  width: 40px;
  height: auto;
  margin-top: 0;
  img {
    width: 100%;
  }
  &:after {
    display: none;
  }
}
.up {
  position: fixed;
  z-index: 4;
  right: 30px;
  bottom: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  background: #434B5C;
  box-shadow: 0px 8px 20px -8px rgba(75, 78, 83, 0.16);
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  svg {
    width: 24px;
    height: auto;
  }
  &--active {
    opacity: 1;
    pointer-events: all;
  }
}
.navigation {
  padding: 50px 0;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__link {
    font-size: 24px;
    line-height: 130%;
    color: #FBFBFB;
    &:hover {
      color: #007aff;
    }
  }
}
.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(24, 32, 48, 0.55);
  backdrop-filter: blur(1px);
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 945px;
    max-width: 100%;
    position: relative;
    background: #2B3450;
    border-radius: 16px;
    padding: 70px 50px 90px;
    margin: 0 auto;
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      bottom: -350px;
      right: -350px;
      width: 700px;
      height: 700px;
      background: linear-gradient(90deg, rgba(0, 219, 222, 0.70) 0%, rgba(252, 0, 255, 0.70) 100%);
      border-radius: 50%;
      filter: blur(141.5px);
      pointer-events: none;
    }
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__line {
    position: absolute;
    top: -120px;
    left: -120px;
    width: 515px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img {
    position: absolute;
    z-index: 1;
    right: -45px;
    bottom: -20px;
    width: 220px;
    img {
      width: 100%;
    }
  }
  &__close {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 25px;
    height: 25px;
    background: transparent;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__content {
    position: relative;
    z-index: 2;
    max-width: 715px;
  }
  &__title {
    font-size: 36px;
    line-height: 130%;
    font-weight: 700;
    color: #FBFBFB;
  }
  &__text {
    margin-top: 24px;
    p {
      font-size: 18px;
      line-height: 130%;
      color: #CED2DC;
    }
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 40px;
  }
  &__btn {
    width: 250px;
    height: 48px;
    margin-right: 20px;
    margin-bottom: 10px;
  }
  &__btn2 {
    width: 250px;
    height: 48px;
    margin-bottom: 10px;
  }
}
.tabs__content {
  display: none;
}
.tabs__content--active {
  display: block;
}
.left-animation {
  transform: translate(-120%, 0);
  opacity: 0;
  pointer-events: none;
  transition: all 0.8s;
}
.left-animation.active {
  transform: translate(0,0);
  opacity: 1;
  pointer-events: all;
}
.right-animation {
  transform: translate(120%, 0);
  opacity: 0;
  pointer-events: none;
  transition: all 0.8s;
}
.right-animation.active {
  transform: translate(0,0);
  opacity: 1;
  pointer-events: all;
}
.rotate__animation {
  transform: rotate(-180deg);
  transition: all 0.8s;
  opacity: 0;
  pointer-events: none;
}
.rotate__animation.active {
  transform: rotate(0);
  opacity: 1;
  pointer-events: all;
}







@media screen and (max-width: 992px) {
  .btn-fixed {
    display: none;
  }
  .display2 {
    font-size: 32px;
  }
  .up {
    right: 15px;
    width: 60px;
    height: 60px;
    svg {
      width: 20px;
    }
  }
  .modal {
    &__dialog {
      padding: 50px 30px;
    }
    &__img {
      display: none;
    }
    &__content {
      max-width: 100%;
    }
    &__title {
      font-size: 28px;
    }
  }
}

@media screen and (max-width: 576px) {
  .display2 {
    font-size: 28px;
  }
  .modal {
    &__dialog {
      padding: 50px 20px 30px;
    }
    &__title {
      font-size: 24px;
    }
    &__btns {
      display: block;
    }
    &__btn {
      margin-right: 0;
    }
  }
}