.footer-top {
  background: #182030;
  padding: 60px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__logo {
    display: block;
    width: 200px;
    img {
      width: 100%;
    }
  }
  &__items {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    margin: 0 60px;
  }
  &__item {
    max-width: 185px;
    &:not(:first-child) {
      margin-left: 24px;
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &-title {
      font-size: 18px;
      line-height: 130%;
      font-weight: 700;
      color: #E5E7ED;
    }
    &-btn {
      width: 12px;
      margin-left: 8px;
      display: none;
      svg {
        width: 100%;
      }
    }
    &-hidden {
      margin-top: 24px;
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 12px;
      li {
        font-size: 14px;
        line-height: 130%;
        color: #8D95A3;
        &:hover {
          color: #E5E7ED;
        }
      }
    }
  }
  &__btn {
    width: 230px;
    height: 44px;
  }
  &__btn2 {
    width: 230px;
    height: 44px;
    margin-top: 32px;
  }
}
.footer-bottom {
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #2A3242;
    padding: 30px 0 15px;
  }
  &__left {
    margin-right: 30px;
    margin-bottom: 15px;
  }
  &__right {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
  }
  &__text {
    font-size: 14px;
    line-height: 130%;
    color: #8D95A3;
  }
  &__links {
    display: flex;
    align-items: center;
  }
  &__link {
    font-size: 14px;
    line-height: 130%;
    color: #E5E7ED;
    &:not(:first-child) {
      margin-left: 15px;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-left: 15px;
  }
  &__social {
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #1E253C;
    border-radius: 7px;
    &:not(:first-child) {
      margin-left: 12px;
    }
    svg {
      max-width: 13px;
      height: auto;
    }
  }
}

@media screen and (max-width: 1150px) {
  .footer-top {
    &__logo {
      width: 180px;
    }
    &__items {
      margin: 0 20px;
    }
    &__item:not(:first-child) {
      margin-left: 20px;
    }
    &__btn,
    &__btn2 {
      width: 220px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer-top {
    padding: 40px 0 0;
    &__inner {
      display: block;
    }
    &__items {
      justify-content: start;
      margin: 32px 0;
    }
    &__item {
      &:not(:first-child) {
        margin: 0 0 0 60px;
      }
    }
    &__btns {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }
    &__btn {
      margin-right: 32px;
      margin-bottom: 20px;
    }
    &__btn2 {
      margin: 0 0 10px 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__item {
      &:not(:first-child) {
        margin: 0 0 0 24px;
      }
    }
  }
  .footer-bottom {
    &__inner {
      display: block;
      padding: 20px 0;
    }
    &__left {
      margin: 0;
    }
    &__right {
      display: block;
      margin: 15px 0 0 0;
    }
    &__socials {
      margin: 15px 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__items {
      display: block;
    }
    &__item {
      max-width: 100%;
      padding: 16px 0;
      border-bottom: 1px solid #e5e7ed;
      &:not(:first-child) {
        margin: 0;
      }
      &:first-child {
        padding-top: 0;
      }
      &-show {
        cursor: pointer;
      }
      &-show--active {
        .footer-top__item-btn {
          svg {
            transform: rotate(-180deg);
          }
        }
      }
      &-hidden {
        display: none;
      }
      &-btn {
        display: block;
        svg {
          transition: all 0.5s;
        }
      }
    }
  }
}