.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  padding: 25px 0 0 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 100px;
  }
  &__logo {
    display: block;
    width: 200px;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    li {
      font-size: 16px;
      line-height: 130%;
      color: #FBFBFB;
    }
    li:not(:first-child) {
      margin-left: 48px;
    }
    li:hover {
      color: #CED2DC;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-left: 20px;
  }
  &__btn {
    width: 230px;
    height: 40px;
  }
  &__btn2 {
    width: 195px;
    height: 40px;
    margin-left: 32px;
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 30px;
  height: 18px;
  cursor: pointer;
  margin-left: 20px;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #FBFBFB;
    border-radius: 3px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 8px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 8px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1150px) {
  .header {
    &__right {
      flex-grow: 0;
      margin-left: 20px;
    }
    &__list {
      li:not(:first-child) {
        margin-left: 20px;
      }
    }
    &__btn2 {
      margin-left: 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__right {
      position: fixed;
      z-index: 10;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: start;
      background: #17283E;
      padding: 100px 15px 50px;
      margin: 0;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
    }
    &__list {
      flex-direction: column;
      li:not(:first-child) {
        margin: 30px 0 0 0;
      }
    }
    &__btns {
      flex-direction: column;
      margin: 30px 0 0 0;
    }
    &__btn2 {
      margin: 30px 0 0 0;
    }
  }
  .burger {
    display: block;
  }
}